<template>
  <div>
    <nav-bar />
    <b-container v-if="fotos && fotos.length > 0">
      <b-row style="margin-top: 20px; display:flex; justify-content:center">
        <b-col lg="10" md="10" sm="12">
          <b-button
            @click="adicionarFotosView = !adicionarFotosView"
            variant="outline-success"
            size="sm"
            class="d-flex justify-content-center align-items-center pt-10 mb-3"
          >
            <b-icon icon="file-plus" />Novas Fotos
          </b-button>
          <b-list-group>
            <b-list-group-item>Fotos da Casa</b-list-group-item>
          </b-list-group>
          <b-row class="mt-3">
            <b-col lg="3" md="4" sm="12" v-for="(foto, index) in fotos" :key="'foto-' + index">
              <b-card
                :img-src="foto.imagem"
                img-alt="Image"
                img-top
                tag="article"
                style="max-width: 500px;"
                class="mb-2"
              >
                <b-button
                  @click="removerFoto(foto.id)"
                  style="width:100%"
                  variant="outline-danger"
                  size="sm"
                  class="d-flex mr-2 mb-2 justify-content-center align-items-center pt-10"
                >
                  <b-icon icon="trash-fill" />Deletar
                </b-button>
              </b-card>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-container>

    <b-container v-else>
      <div
        style="width: 100%;margin-top: 30px; display: flex; flex-direction: column; height: 100%; justify-content: center; align-items: center"
      >
        <img
          style="width: 20%"
          src="data:image/svg+xml;base64,"
        />
        <span style="margin-top: 40px">Nenhuma Foto Cadastrada...</span>
        <div style="margin-top: 20px">
          <b-button
            @click="adicionarFotosView = !adicionarFotosView"
            variant="success"
            size="sm"
            class="d-flex justify-content-center align-items-center pt-10 mb-3"
          >
            <b-icon icon="file-plus" style="margin-right: 10px" />Adicionar Foto
          </b-button>
        </div>
      </div>
    </b-container>

    <b-modal
      style="height: auto"
      title="Nova Foto"
      hide-footer
      id="modal-no-backdrop"
      hide-backdrop
      v-model="adicionarFotosView"
    >
      <b-row>
        <b-col>
          <template>
            <div class="container">
              <div class="large-12 medium-12 small-12 cell">
                <label>
                  Foto:
                  <input type="file" id="file" ref="file" @change="handleFileUpload()" />
                </label>
                <b-button
                  style="width:100%"
                  @click="submitFile()"
                  variant="outline-info"
                  size="sm"
                  class="d-flex mr-2 mb-2 justify-content-center align-items-center pt-10"
                >
                  <b-icon icon="box-arrow-in-up-right" />Enviar
                </b-button>
              </div>
            </div>
          </template>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>

<script>
// import Botao from "../components/Botao";
import NavBar from "../components/NavBar";
import _ from "lodash";
import server from "@/service/server";

export default {
  data() {
    return {
      file: "",
      fotos: [],
      fotosSelecionadas: [],
      adicionarFotosView: false,
    };
  },

  methods: {
    submitFile() {
      let formData = new FormData();
      formData.append("file", this.file);
      this.adicionarFotosView = false;

      server
        .post("/fotos-casa/", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then(() => {
          this.listFotos();
        })
        .catch((e) => {
          alert(JSON.stringify(e.message))
        });
    },
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },

    removerFoto(id) {
      server.delete("/fotos-casa/" + id).then(() => {
        const fotos = _.filter(this.fotos, (foto) => {
          return foto.id !== id;
        });
        this.fotos = fotos;
      });
    },

    async listFotos() {
      const { data } = await server.get("/fotos-casa");

      data.forEach((foto) => {
        foto.caminho = "../../../../backend/assets/uploads/casa/" + foto.imagem;
      });
      this.fotos = [...data];
    },
  },

  components: {
    // "button-icon": Botao,
    "nav-bar": NavBar,
  },

  async created() {
    await this.listFotos();
  },
};
</script>