<template>
  <div>
    <nav-bar />

    <b-container v-if="servicos">
      <b-row style="margin-top: 20px; display:flex; justify-content:center">
        <b-col lg="10" md="10" sm="12">
          <b-button
            @click="adicionarFotosView = !adicionarFotosView"
            variant="success"
            size="sm"
            class="d-flex justify-content-center align-items-center pt-10 mb-3"
          >
            <b-icon icon="file-plus" />
            Adicionar Servi√ßo
          </b-button>
          <b-list-group>
            <b-list-group-item>Servi√ßos Oferecidos</b-list-group-item>
          </b-list-group>
          <b-row class="mt-3">
            <b-col
              lg="3"
              md="4"
              sm="1"
              v-for="(servico, index) in servicos"
              :key="'servico-' + index"
            >
              <b-card
                :img-src="servico.imagem"
                img-alt="Image" 
                img-top
                tag="article"
                style="max-width: 20rem;"
                class="mb-2"
              >

                <b-card-text class="mb-4">
                  {{ servico.descricao }}
                </b-card-text>

                <b-button
                  @click="removerServico(servico)"
                  style="width:100%"
                  variant="outline-danger"
                  size="sm"
                  class="d-flex mr-2 mb-2 justify-content-center align-items-center pt-10"
                >
                  <b-icon icon="trash-fill" />Deletar
                </b-button>
              </b-card>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-container>

    <b-container v-else>
      <div style="width: 100%;margin-top: 30px; display: flex; flex-direction: column; height: 100%; justify-content: center; align-items: center">
        <img style="width: 20%" src="data:image/svg+xml;base64," />
        <span style="margin-top: 40px">
            Nenhum Servi√ßo Cadastrado...
        </span>
        <div style="margin-top: 20px">
            <b-button @click="adicionarFotosView = !adicionarFotosView" variant="success" size="sm" class="d-flex justify-content-center align-items-center pt-10 mb-3">
                <b-icon icon="file-plus" style="margin-right: 10px"/>
                Adicionar Servi√ßo
            </b-button>
        </div>
      </div>
    </b-container>
        <!-- Modal -->
    <div>
      <b-modal
        style="height: auto"
        title="Novo Servi√ßo"
        hide-footer
        id="modal-no-backdrop"
        hide-backdrop
        v-model="adicionarFotosView"
      >
        <b-row>
          <b-col>
            <template>
              <div class="container">
                <div class="large-12 medium-12 small-12 cell">
                  <label>T√≠tulo</label>
                  <b-form-input
                    id="form-titulo"
                    v-model="servico.titulo"
                    type="text"
                  ></b-form-input>
                  <label>Descri√ß√£o</label>
                  <b-form-input
                    id="form-descricao"
                    v-model="servico.descricao"
                    type="text"
                    style="margin-bottom:10px"
                  ></b-form-input>
                  <label>
                    Foto:
                  </label>
                  <br />
                  <input
                    type="file"
                    id="file"
                    ref="file"
                    @change="handleFileUpload()"
                  >
                  <b-button
                    style="width:100%;margin-top: 20px"
                    @click="submitFile()"
                    variant="success"
                    size="sm"
                    class="d-flex mr-2 mb-2 justify-content-center align-items-center pt-10"
                  >
                    Salvar Servi√ßo
                  </b-button>
                </div>
              </div>
            </template>
          </b-col>
        </b-row>
      </b-modal>
    </div>
    <!-- Fecha modal -->

    <!-- </b-container> -->

  </div>
</template>

<script>
import NavBar from "../components/NavBar";
import server from "../service/server";
import _ from "lodash";

export default {
  data() {
    return {
      servicos: [],
      servico: {},
      servicoSelecionado: {},
      file: "",
      lastId: "",
      adicionarFotosView: false,
    };
  },

  created() {
    this.listTiposEventos();
  },

  methods: {
    submitFile() {
      let formData = new FormData();

      formData.append("file", this.file);
      formData.append("servico", this.servico);

      this.salvarServico(this.servico, formData);
    },
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },

    async listTiposEventos() {
      const { data } = await server.get("/servicos")
      this.servicos = data
    },

    updateServico(servico) {
      this.servicoSelecionado = servico;
    },

    removerServico({ id }) {
      server.delete("/servicos/" + id).then(() => {
        const newServicosList = _.filter(this.servicos, (servico) => {
          return servico.id !== id;
        });

        this.servicos = newServicosList;
      });
    },

    salvarServico(newServico, formData) {
      server.post(
          `/servicos/${newServico.titulo}/${newServico.descricao}`,
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then(() => {
          this.adicionarFotosView = false;
          this.listTiposEventos();
          this.servico = {}
        })
        .catch(() => {
        });
    },
  },

  components: {
    "nav-bar": NavBar,
  },
};
</script>
